import {
	request
} from '../utils/request.js'
import {
	appConfig
} from '../config/config.js'


export function agree(data) {
	return request.post(appConfig.WEB_API + '/app/likeOrCollection/likeOrCollectionByDTO', data)
}

export function cancelAgree(data) {
	return request.post(appConfig.WEB_API + '/app/likeOrCollection/likeOrCollectionByDTO', data)
}

export function isAgree(data) {
	return request.post(appConfig.WEB_API + '/app/likeOrCollection/isLikeOrCollection', data)
}

export function isAgrees(data) {
	return request.post(appConfig.WEB_API + '/app/likeOrCollection/isLikeOrCollections', data)
}

export function getAllAgreeAndCollection(page, limit, params) {
	return request.get(appConfig.WEB_API + `/app/likeOrCollection/getNoticeLikeOrCollection/${page}/${limit}`, params)
}

export function getAllCollection(page, limit, params) {
	return request.get(appConfig.WEB_API + `/app/user/getTrendByUser/${page}/${limit}`, params)
}


export function collection(data) {
	return request.post(appConfig.WEB_API + '/app/agreeCollect/collection', data)
}

export function cancelCollection(data) {
	return request.post(appConfig.WEB_API + '/app/agreeCollect/cancelCollection', data)
}